.catalog-product-wrapper {
  @include bp-tablet {
    position: relative;
    display: flex;
    width: 100%;
    margin-top: -1px;
    background-color: $white-medium-light;

    &::after {
      content: "";
      position: absolute;
      bottom: -14px;
      left: 0;
      width: 100%;
      height: 24px;
      background: url("../img/bg-zigzag-fill.svg") repeat-x center top;
      background-size: auto 22.8px;
    }
  }

  @include bp-desktop {
    background-size: auto 30px;

    &::after {
      bottom: -18px;
      height: 34px;
      background: url("../img/bg-zigzag-fill.svg") repeat-x center top;
      background-size: auto 37px;
    }
  }
}
